<template>
    <main id="main">
            <section1-component />
            <section2-component :sec2data="sec2data"/>
            <section3-component :sec3data="sec3data"/>
            <section4-component />
            <section5-component :sec5data="sec5data"/>
            </main>
</template>

<script>
import Section1Component from './main/Section1Component.vue'
import Section2Component from './main/Section2Component.vue'
import Section3Component from './main/Section3Component.vue'
import Section4Component from './main/Section4Component.vue'
import Section5Component from './main/Section5Component.vue'
import axios from 'axios'

export default {
    name:"MainComponent",
    data(){
        return{
            sec2data:{
                top: [],
                bottom:[]
            },

            sec3data:{
                proTab:[],
                product1:[],
                product2:[],
                product3:[],
                product4:[],
            },

            sec5data:{
                newsT:"",
            thum:{}
            ,
            nList:[],
            inq:{},
            shop:{}
            }
           
        }
    },
     components: {
        'section1-component':Section1Component,
        'section2-component':Section2Component,
        'section3-component':Section3Component,
        'section4-component':Section4Component,
        'section5-component':Section5Component,
    },

    created() {
        axios({
            url: './data/product.json',
            method: 'GET'
        })
            .then((res) => {
                this.sec3data.proTab = res.data.proTab;
                this.sec3data.product1 = res.data.product1;
                this.sec3data.product2 = res.data.product2;
                this.sec3data.product3 = res.data.product3;
                this.sec3data.product4 = res.data.product4;
            })
            .catch((err) => {
                console.log(err);
            });


        axios({
            url: './data/newitem.json',
            method: 'GET'
        })
            .then((res) => {
                this.sec2data.top = res.data.top;
                this.sec2data.bottom = res.data.bottom;
            })
            .catch((err) => {
                console.log(err);
            });


        axios({
            url: './data/notice.json',
            method: 'GET'
        })
            .then((res) => {
                this.sec5data.newsT = res.data.newsT;
                this.sec5data.thum = res.data.thum;
                this.sec5data.nList = res.data.nList;
                this.sec5data.inq = res.data.inq;
                this.sec5data.shop = res.data.shop;

            })
            .catch((err) => {
                console.log(err);
            });

    }
    }


</script>

<style scoped>
#wrap #main{width:100%; height:4167px; position:relative; z-index:2; padding-top:140px;}
</style>