<template>
    <section id="section3" class="section">
                <div class="title">BEST PRODUCT</div>
                <p>{{text}}</p>
                
                <div class="pro-tab">
                    <ul>
                        <li v-for="(item,index) in sec3data.proTab" :class="['menu', 'menu'+(index+1), {'on':isOn[index]}]" :key="item.title"><a @click.prevent="tabClickMethod(index)" href="#" :class="'menu-btn'+(index+1)" :title="item.title">{{item.title}}</a></li>
                    </ul>
                </div>
                <div class="pro-list">
                    <ul :class="['pro-wrap', 'pro-wrap-1', {'on':isOn[0]}]">
                        <li v-for="(pro1,index) in sec3data.product1" :key="pro1.name"><a @click="clickStop" href="#"><img :src="'./img/pen'+(index+1)+'.jpg'" :alt="pro1.name"></a>
                            <div class="info">
                                <div class="category">{{pro1.cate}}</div>
                                <div class="name">{{pro1.name}}</div>
                            </div>
                        </li>
                    </ul>

                   <ul :class="['pro-wrap', 'pro-wrap-2', {'on':isOn[1]}]">
                        <li v-for="(pro2,index) in sec3data.product2" :key="pro2.name"><a @click="clickStop" href="#"><img :src="'./img/pen-s2/pen2-'+(index+1)+'.jpg'" :alt="pro2.name"></a>
                            <div class="info">
                                <div class="category">{{pro2.cate}}</div>
                                <div class="name">{{pro2.name}}</div>
                            </div>
                        </li>
                    </ul>
                  
                    <ul :class="['pro-wrap', 'pro-wrap-3', {'on':isOn[2]}]">
                        <li v-for="(pro3,index) in sec3data.product3" :key="pro3.name"><a @click="clickStop" href="#"><img :src="'./img/pen-s3/pen3-'+(index+1)+'.jpg'" :alt="pro3.name"></a>
                            <div class="info">
                                <div class="category">{{pro3.cate}}</div>
                                <div class="name">{{pro3.name}}</div>
                            </div>
                        </li>
                    </ul>

                        <ul :class="['pro-wrap', 'pro-wrap-4',{'on':isOn[3]}]">
                            <li v-for="(pro4,index) in sec3data.product4" :key="pro4.name"><a @click="clickStop" href="#"><img :src="'./img/pen-s4/pen4-'+(index+1)+'.jpg'" :alt="pro4.name"></a>
                                <div class="info">
                                    <div class="category">{{pro4.cate}}</div>
                                    <div class="name">{{pro4.name}}</div>
                                </div>
                            </li>
                        </ul>
                    </div>

                <div class="pro-info">
                    <div class="wrap">
                        <span class="left-arr">
                            <img src="../../../../public/img/left-arr.gif" alt="">
                        </span>
                        <span class="right-arr">
                            <img src="../../../../public/img/right-arr.gif" alt="">
                        </span>
                        <div class="dns">
                            <div><img src="../../../../public/img/cursor.gif" alt=""></div>
                            <p>DRAG & SCROLL</p>
                        </div>
                    </div>
                </div>
            </section>
</template>

<script>
import $ from 'jquery'

export default {
    name:"Section3Component",
    props:['sec3data'],
     data() {
            return {
                isOn: [true,false,false,false],
                text:"언제 어디서나 생활 속에 함께하는 모나미",
        }
    },

    
    methods:{
        tabClickMethod(index){
            this.isOn = Array(4).fill(false);
            this.isOn[index] = true;
            $(`.pro-list .pro-wrap-${index + 1}`).css('left', '0px')
        },
        clickStop(event){
            event.preventDefault();
        }
    },
    
    mounted(){
    let dragging = false;
    let startLeft, mouseX, initialLeft;

    $('.pro-list .pro-wrap-1, .pro-list .pro-wrap-2, .pro-list .pro-wrap-3, .pro-list .pro-wrap-4').on('mousedown', function(e){
        dragging = true;
        startLeft = e.pageX;
        initialLeft = parseInt($(this).css('left'));
        e.preventDefault();
        e.stopPropagation();
    });

    $('a').on('mousedown', function(e){
        e.preventDefault();
    });

    $(document).on('mousemove', function(e){
        if(dragging){
            mouseX = e.pageX;
            let movement = mouseX - startLeft;
            $(document).find('.pro-list .pro-wrap-1, .pro-list .pro-wrap-2, .pro-list .pro-wrap-3, .pro-list .pro-wrap-4').each(function(){
                let newLeft = initialLeft + movement;

                if($(this).hasClass('pro-wrap-1')){    
                    newLeft = Math.min(0, Math.max(-950, newLeft)); 
                } else if($(this).hasClass('pro-wrap-2')){
                    newLeft = Math.min(0, Math.max(-3800, newLeft)); 
                }
                else if ($(this).hasClass('pro-wrap-3')) {
                    newLeft = Math.min(0, Math.max(-3800, newLeft)); 
                }

                else if ($(this).hasClass('pro-wrap-4')) {
                    newLeft = Math.min(0, Math.max(-100, newLeft)); 
                }

                $(this).css('left', newLeft + 'px');
            });
        }
    });

    $(document).on('mouseup', function(){
        dragging = false;
    });
    
 

   


     
}

    
  
}
</script>

